.marquee_section{
    display: flex;
    background-color: #0E1216;
    color: white;
    padding: 1rem;
    /* width: 100%; */
    /* height: 30px; */
    /* position: absolute; */
    overflow: hidden;
    
        transform: rotate(3deg);
    justify-content: flex-start;
        align-items: center;
        width: max-content;
}
.marquee_section h1{
    font-family: Clash Display Variable;
font-size: 28px;
font-weight: 400;
line-height: 34px;
letter-spacing: 0.06em;
text-align: left;
    text-transform: uppercase;
    /* -webkit-text-stroke-width: 0.1px; */
    animation: marquee 10s linear infinite;
}

/* 
@keyframes marquee {
    0% { transform: translateX(0%); }
    
    100% { transform: translateX(-100%); }
  } */
  .marquee {
    transform: rotate(3deg);
    position: relative;
    width: 100vw;
    max-width: 100%;
    height: 200px;
    overflow-x: hidden;
  }
  .marquee .content{
    animation: marquee 32s linear infinite;
    display: flex;
  }
  .marquee .content h1{
    margin: 0 2rem;
    font-family: Clash Display Variable;
    font-size: 28px;
    font-weight: 400;
    line-height: 34px;
    letter-spacing: 0.06em;
  }
  .track {
    
    position: absolute;
    white-space: nowrap;
    will-change: transform;
    
    background: rgb(0,0,0);
    background: linear-gradient(140deg, rgba(0,0,0,1) 0%, rgba(69,69,69,1) 26%, rgba(255,255,255,1) 100%);
    /* background-color: #0E1216; */
    color: white;
    padding: 1rem;
  }
  
  @keyframes marquee {
    from { transform: translateX(-50%); }
    to { transform: translateX(0%); }
  }


.anim{
    max-height: 40px;
    overflow: hidden;

    padding-top: 0;
    
}
/* .anim p:not(.special_heading):hover ~ p{
    transform: translateY(-25px);
}
.anim p:not(.special_hide):hover {
    transform: translateY(-25px);
} */
.inner_anim:hover{
    transform: translateY(-28px);
}

.inner_anim{
    transition: transform .5s;
}
.anim p{
    cursor: pointer;
transition: transform .5s;
margin: 10px 0;
text-align: left;
height: 17px;
font-family: 'Inter';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 17px;
/* identical to box height */
letter-spacing: 0.03em;
text-transform: capitalize;
color: #0E1216;
}
.special_hide{

color: #846ED4  !important;

}
.marquee{
    margin-top: 10rem;
}